<template>
	<div class="login-body">
		<div class="back" @click="back">
			<i class="icon-back"></i>
		</div>
		<div class="login-wrap">
			<div class="login-anime">
				<div class="login-logo"><i class="icon-blog"></i></div>
				<div class="login-from">
					<div class="login-row">
						<i class="icon-user"></i>
						<input type="text" ref="username" v-model="username" placeholder="Username" />
					</div>
					<div class="login-row">
						<i class="icon-password"></i>
						<input type="password" ref="password" v-model="password" placeholder="Password"/>
					</div>
					<button type="button" @click="login" class="login-btn">
						<span v-if="!islogin">Sign In</span>
            <div style="width: 20px; margin: 0 auto;"  v-if="islogin">
						  <loading></loading>
            </div>
					</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import loading from '@/components/public/loading'
	export default {
		data() {
			return {
				islogin: false,
				username: "",
				password: ""
			}
		},
		mounted() {

      //back箭头动画
			this.$anime({
				targets: '.back',
				opacity: 1,
				translateX: -50,
				duration: 800,
				delay: 400,
				easing: 'easeInOutSine'
			});
			//登录框动画
			this.$anime({
				targets:'.login-anime',
				opacity: 1,
				duration: 800,
				easing: 'easeInOutSine'
			})

		},
		methods: {
		  //登录失败
			error() {
        this.$layer.msg("密码错误");

        this.islogin = false;
				this.$anime({
					targets: '.login-from',
					translateX: [{
							value: 10,
							duration: 100,
							easing: 'easeInOutSine'
						},
						{
							value: -10,
							duration: 100,
							easing: 'easeInOutSine'
						}
					],
					loop:3
				});
			},
      //返回首页
      back(){
				this.$anime({
					targets: '.back',
					opacity: 1,
					translateX: -200,
					duration: 200,
					easing: 'easeInOutSine',
					complete: (anim) => {
						this.$router.push('/');
					}
				});
			},
      login() {
				this.islogin = true;
				let username = this.$refs.username.value;
				let password = this.$refs.password.value;

				if(username == "" || password == "") {
					this.error();
					return false;
				}

        //登录
				Bmob.User.logIn(username, password, {
					success: (user) => {
						if(user.attributes.code == undefined) {
							this.$anime({
								targets: '.login-btn',
								width: {
									value: 44,
									duration: 400,
									easing: 'easeInOutQuart'
								},
								scale: {
									value: 120,
									duration: 400,
									delay: 800,
									easing: 'easeInOutQuart'
								},
								complete: (anim) => {
									this.$router.push('/admin');
								}
							});
						} else {
							this.error()
						}
					},
					error: (user, error) => {
						this.error();
					}
				});
			}
		},
		components: {
			loading
		}
	}
</script>

<style>

</style>



